body {
	background: #030a1a;
}
#skip {
	position: relative;
	z-index: 100000;
}
#skip a {
	background: #030a1a;
	color: #b5b5b5;
	position: absolute;
	padding: 20px 40px;
	left: -400px;
	top: 5px;
}
#skip a:active,
#skip a:focus {
	left: 5px;
}

.container {
	width: 90%;
	padding: 0 20px;
	margin: 0 auto;
	max-width: 1160px;
	box-sizing: border-box;
}

.section {
	padding: 120px 0;
}

.section > h2 {
	font-size: 50px;
	line-height: 1;
	text-align: center;
	margin-bottom: 20px;
	color: #dadada;
}
.section > span {
	display: block;
	text-align: center;
	font-size: 16px;
	text-decoration: underline;
	text-underline-position: under;
	margin-bottom: 20px;
	color: #666;
}
.section > p {
	font-size: 22px;
	line-height: 1.5;
	text-align: center;
	font-weight: 300;
	color: #b5b5b5;
	margin-bottom: 70px;
	padding: 0 20px;
	word-break: keep-all;
}
@media (max-width: 960px) {
	.section > h2 {
		font-size: 40px;
	}
	.section > p {
		font-size: 18px;
		margin-bottom: 60px;
	}
}
@media (max-width: 600px) {
	.section > h2 {
		font-size: 30px;
	}
	.section > p {
		font-size: 16px;
		margin-bottom: 50px;
	}
}

/* 폰트 */
.nexon {
	font-family: "NexonLv1Gothic";
	font-weight: 400;
}

/* 배경 */
.gray {
	background-color: #f5f5f5;
}
/* fixed */
body {
	overflow: auto;
}
body.fixed {
	height: 100vh;
	overflow: hidden;
}
